<template>
	<div id="detailselectbar">
		<ul>
			<li :class="[fontgray,fl,{current:sshow}]" @tap='sel'></li>
			<li :class="[fontgray,fl,{current:fshow}]" @tap='sel'></li>
			<li :class="[fontgray,fl,{current:kshow}]" @tap='sel' id="kline">
				<span>K</span>
				<transition name="topdown" mode="out-in">
					<ol v-show='olshow'>
						<li class="fontwhite  fl" @tap.stop="olclick">1</li>
						<li class="fontwhite  fl" @tap.stop="olclick">5</li>
						<li class="fontwhite  fl" @tap.stop="olclick">15</li>
						<li class="fontwhite  fl" @tap.stop="olclick">30</li>
						<li class="fontwhite  fl" @tap.stop="olclick">K</li>
					</ol>
				</transition>
			</li>
			<li :class="[fontgray,fl,{current:pshow}]" @click='sel'></li>
			<li :class="[fontgray,fl,{current:jshow}]" @click='sel'></li>
		</ul>
	</div>
</template>

<script>
	var yellow = require('../assets/img/sanjiao01.png');
	var blue = require('../assets/img/sanjiao02.png');
	export default {
		name: 'detailselectbar',
		data() {
			return {
				sshow: false,
				fshow: true,
				kshow: false,
				pshow: false,
				jshow: false,
				olshow: false
			}
		},
		watch: {
			kshow: function(n, o) {
				if(n == true) {
					$('#kline').css('background-image', 'url(' + yellow + ')');
				} else {
					$('#kline').css('background-image', 'url(' + blue + ')');
				}
			}
		},
		methods: {
			olclick: function(e) {
				var oltex = e.target.innerText;
				//				console.log(oltex);
				$(e.target).parent().parent('li').children('span').text(oltex);
				this.olshow = false;
			},
			sel: function(e) {
				var tex = e.target.innerText;
				//				console.log(e.target);
				switch(tex) {
					case '':
						this.sshow = true;
						this.fshow = false;
						this.kshow = false;
						this.pshow = false;
						this.jshow = false;
						this.olshow = false;
						break;
					case '':
						this.sshow = false;
						this.fshow = true;
						this.kshow = false;
						this.pshow = false;
						this.jshow = false;
						this.olshow = false;
						break;
					case '':
						this.sshow = false;
						this.fshow = false;
						this.kshow = false;
						this.pshow = true;
						this.jshow = false;
						this.olshow = false;
						break;
					case '':
						this.sshow = false;
						this.fshow = false;
						this.kshow = false;
						this.pshow = false;
						this.jshow = true;
						this.olshow = false;
						break;
					case 'K':

						this.sshow = false;
						this.fshow = false;
						this.kshow = true;
						this.pshow = false;
						this.jshow = false;
						if(this.olshow == false) {
							this.olshow = true;
						} else {
							this.olshow = false;
						}
						break;
					default:
						this.sshow = false;
						this.fshow = false;
						this.kshow = true;
						this.pshow = false;
						this.jshow = false;
						if(this.olshow == false) {
							this.olshow = true;
						} else {
							this.olshow = false;
						}
						break;
				}
			}
		},
		computed: {
			fontwhite() {
				return 'fontwhite';
			},
			fl() {
				return 'fl';
			},
			fontgray() {
				return 'fontgray';
			}
		}
	}
</script>

<style scoped lang="less">
	@import url("../assets/css/main.less");
	@height: 40px;
	#detailselectbar>ul>li.current {
		color: #ffd400;
		border-bottom: 3px solid #FFD400;
	}
	li{
		font-size: 16px;
	}
	#detailselectbar {
		width: 100%;
		height: @height;
	}
	
	#detailselectbar>ul {
		width: 100%;
		height: @height;
		border-top: 1px solid #1b1b26;
		border-bottom: 1px solid #1b1b26;
		position: relative;
	}
	
	#detailselectbar>ul>li {
		width: 20%;
		height: @height;
		line-height: @height;
		text-align: center;
		background-color: #242633;
		border-bottom: 3px solid transparent;
		z-index: 6;
	}
	
	#detailselectbar>ul>li>ol {
		width: 414px;
		height: @height;
		position: absolute;
		z-index: 5;
		left: 0;
		top: @height;
		background: #2d3040;
		border-bottom: 1px solid #1b1b26;
	}
	
	#detailselectbar>ul>li:nth-child(3) {
		background-image: url('../assets/img/sanjiao02.png');
		background-repeat: no-repeat;
		background-size: 5px;
		background-position: 60px 23px;
	}
	
	#detailselectbar>ul>li>ol>li {
		width: 20%;
		text-align: center;
	}
	@media(max-width:370px) {
#detailselectbar>ul>li.current {
		color: #ffd400;
		border-bottom: 3px solid #FFD400;
	}
	li{
		font-size: 16px*@ip5;
	}
	#detailselectbar {
		width: 100%;
		height: @height*@ip5;
	}
	
	#detailselectbar>ul {
		width: 100%;
		height: @height*@ip5;
		border-top: 1px solid #1b1b26;
		border-bottom: 1px solid #1b1b26;
		position: relative;
	}
	
	#detailselectbar>ul>li {
		width: 20%;
		height: @height*@ip5;
		line-height: @height*@ip5;
		text-align: center;
		background-color: #242633;
		border-bottom: 3px solid transparent;
		z-index: 6;
	}
	
	#detailselectbar>ul>li>ol {
		width: 414px*@ip5;
		height: @height*@ip5;
		position: absolute;
		z-index: 5;
		left: 0;
		top: @height*@ip5;
		background: #2d3040;
		border-bottom: 1px solid #1b1b26;
	}
	
	#detailselectbar>ul>li:nth-child(3) {
		background-image: url('../assets/img/sanjiao02.png');
		background-repeat: no-repeat;
		background-size: 5px*@ip5;
		background-position: 60px*@ip5 23px*@ip5;
	}
	
	#detailselectbar>ul>li>ol>li {
		width: 20%;
		text-align: center;
	}
}
/*ip6*/
@media (min-width:371px) and (max-width:410px) {
    #detailselectbar>ul>li.current {
		color: #ffd400;
		border-bottom: 3px solid #FFD400;
	}
	li{
		font-size: 16px*@ip6;
	}
	#detailselectbar {
		width: 100%;
		height: @height*@ip6;
	}
	
	#detailselectbar>ul {
		width: 100%;
		height: @height*@ip6;
		border-top: 1px solid #1b1b26;
		border-bottom: 1px solid #1b1b26;
		position: relative;
	}
	
	#detailselectbar>ul>li {
		width: 20%;
		height: @height*@ip6;
		line-height: @height*@ip6;
		text-align: center;
		background-color: #242633;
		border-bottom: 3px solid transparent;
		z-index: 6;
	}
	
	#detailselectbar>ul>li>ol {
		width: 414px*@ip6;
		height: @height*@ip6;
		position: absolute;
		z-index: 5;
		left: 0;
		top: @height*@ip6;
		background: #2d3040;
		border-bottom: 1px solid #1b1b26;
	}
	
	#detailselectbar>ul>li:nth-child(3) {
		background-image: url('../assets/img/sanjiao02.png');
		background-repeat: no-repeat;
		background-size: 5px*@ip6;
		background-position: 60px*@ip6 23px*@ip6;
	}
	
	#detailselectbar>ul>li>ol>li {
		width: 20%;
		text-align: center;
	}
}
</style>


